space-between;justify-content:space-between;margin:20px 0px 20px 20px}.button__option{border:1px solid gray;border-radius:3px;padding:5px 10px;cursor:pointer}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;background-color:#17171a;color:#8892b0;font-family:"Kanit", sans-serif;-webkit-font-smoothing:antialiased}

